      { name: "@vue/cli-service", url: "https://www.npmjs.com/package/@vue/cli-service" },
      { name: "@vue/compiler-sfc", url: "https://www.npmjs.com/package/@vue/compiler-sfc" },
      { name: "@monaco-editor/loader", url: "https://www.npmjs.com/package/@monaco-editor/loader" },
    ];

    onMounted(() => {
      if (typeof props.load === "function") {
        props.load(lnbRef.value);
      }
    });

    return { store, state, lnbRef, nav, dependencies };
  },
};
